<template>
  <div class="star" :class="'star-' + size">
    <span class="star-item" v-for="(starClass, index) in starClasses" :key="index" :class="starClass"></span>
  </div>
</template>

<script>
export default {
  props: {
    score: Number,
    size: Number
  },
  computed: {
    //*评分的计算属性 根据评分显示star
    starClasses () {
      let {score} = this;
      const CLASS_ON = 'on';
      const CLASS_HALF = 'half';
      const CLASS_OFF = 'off'
      let starClasses = [];
      const ON_COUNT = Math.floor(score);
      const HALF_COUNT = score - ON_COUNT;

      for (let i = 0; i < ON_COUNT; i++) {
        starClasses.push(CLASS_ON);
      }

      if (HALF_COUNT * 10 >= 5) {
        starClasses.push(CLASS_HALF);
      }

      while (starClasses.length < 5) {
        starClasses.push(CLASS_OFF)
      }
      return starClasses;
    }
  }
};
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import '../../common/stylus/mixins.styl';
.star { // 2x图 3x图
  float: left;
  font-size: 0;

  .star-item {
    display: inline-block;
    background-repeat: no-repeat;
  }

  &.star-48 {
    .star-item {
      width: 20px;
      height: 20px;
      margin-right: 22px;
      background-size: 20px 20px;

      &:last-child {
        margin-right: 0;
      }

      &.on {
        bg-image('./images/star48_on');
      }

      &.half {
        bg-image('./images/star48_half');
      }

      &.off {
        bg-image('./images/star48_off');
      }
    }
  }

  &.star-36 {
    .star-item {
      width: 15px;
      height: 15px;
      margin-right: 6px;
      background-size: 15px 15px;

      &:last-child {
        margin-right: 0;
      }

      &.on {
        bg-image('./images/star36_on');
      }

      &.half {
        bg-image('./images/star36_half');
      }

      &.off {
        bg-image('./images/star36_off');
      }
    }
  }

  &.star-24 {
    .star-item {
      width: 10px;
      height: 10px;
      margin-right: 3px;
      background-size: 10px 10px;

      &:last-child {
        margin-right: 0;
      }

      &.on {
        bg-image('./images/star24_on');
      }

      &.half {
        bg-image('./images/star24_half');
      }

      &.off {
        bg-image('./images/star24_off');
      }
    }
  }
}
</style>



