<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score" v-text="shopMsg.score"></h1>
          <div class="title">综合评分</div>
          <div class="rank" v-text="'高于周边商家' + shopMsg.rankRate + '%'"></div>
        </div>

        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <Pingjia :score="shopMsg.serviceScore" :size="36"/>
            <span class="score" v-text="shopMsg.serviceScore"></span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <Pingjia :score="shopMsg.foodScore" :size="36"/>
            <span class="score" v-text="shopMsg.foodScore"></span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery" v-text="shopMsg.deliveryTime + '分钟'"></span>
          </div>
        </div>
      </div>

      <div class="split"></div>
      <div class="ratingselect">
        <div class="rating-type border-1px">
          <span
            class="block positive"
            :class="{active: choosePingJia === 2}"
            @click="choosePingJia = 2"
          >
            全部
            <span class="count" v-text="shopMsg.ratingCount"></span>
          </span>
          <span
            class="block positive"
            :class="{active: choosePingJia === 0}"
            @click="choosePingJia = 0"
          >
            满意
            <span class="count" v-text="positiveSize"></span>
          </span>
          <span
            class="block negative"
            :class="{active: choosePingJia === 1}"
            @click="choosePingJia = 1"
          >
            不满意
            <span class="count" v-text="shopMsg.ratingCount - positiveSize"></span>
          </span>
        </div>
        <div class="switch" :class="{on: textInit}">
          <span class="iconfont icon-check_circle" @click="textInit = !textInit"></span>
          <span class="text">只看有内容的评价</span>
        </div>
      </div>
      <div class="rating-wrapper">
        <ul>
          <li class="rating-item" v-for="(pingjia, index) in  filterPingJia" :key="index">
            <div class="avatar">
              <img width="28" height="28" :src="pingjia.avatar">
            </div>
            <div class="content">
              <h1 class="name" v-text="pingjia.username">aa</h1>
              <div class="star-wrapper">
                <Pingjia :score="pingjia.score" :size="24"/>
                <span class="delivery" v-text="pingjia.deliveryTime"></span>
              </div>
              <p class="text" v-text="pingjia.text"></p>
              <div class="recommend">
                <span
                  class="iconfont"
                  :class="pingjia.rateType === 0 ? 'icon-thumb_up' : 'icon-thumb_down'"
                ></span>
                <span
                  class="item"
                  v-for="(item,index) in pingjia.recommend"
                  :key="index"
                  v-text="item"
                ></span>
              </div>
              <!-- //*v-text里用过滤器失效，原因是在vue2.0里 管道符‘|’只能用在mousetache和v-bind中 -->
              <div class="time">{{pingjia.rateTime | dataFilter}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Pingjia from "../../../components/Pingjia/Pingjia";
import { mapState, mapGetters } from "vuex";
import BScroll from "better-scroll";

export default {
  components: {
    Pingjia
  },
  data() {
    return {
      textInit: false,
      choosePingJia: 2
    };
  },
  computed: {
    ...mapState(["shopPingJia", "shopMsg"]),
    ...mapGetters(["positiveSize"]),
    filterPingJia() {
      return this.shopPingJia.filter(item => {
        return (
          (this.choosePingJia === 2 || this.choosePingJia === item.rateType) &&
          (!this.textInit || item.text != "")
        );
        //方法三
        //根据以下条件过滤数组
        // if (this.textInit) {
        //   //只选择有评价文本的
        //   if (item.text != "") {
        //     if (this.choosePingJia === 0) {
        //       //选择评价满意的
        //       return item.rateType === 0;
        //     } else if (this.choosePingJia === 1) {
        //       //选择评价不满意的
        //       return item.rateType === 1;
        //     } else {
        //       // 选择全部评价
        //       return true;
        //     }
        //   } else {
        //     return false;
        //   }
        // } else {
        //   //有无评价文本都选择
        //   if (this.choosePingJia === 0) {
        //     //选择评价满意的
        //     return item.rateType === 0;
        //   } else if (this.choosePingJia === 1) {
        //     //选择评价不满意的
        //     return item.rateType === 1;
        //   } else {
        //     // 选择全部评价
        //     return true;
        //   }
        // }
      });
      //方式二
      // let filterPingJia = [];
      // if (this.choosePingJia === 0) {
      //   filterPingJia = this.shopPingJia.filter(item => {
      //     return item.rateType === 0;
      //   });
      // } else if (this.choosePingJia === 1) {
      //   filterPingJia = this.shopPingJia.filter(item => {
      //     return item.rateType === 1;
      //   });
      // } else {
      //   filterPingJia = this.shopPingJia;
      // }
      // if (this.textInit) {
      //   filterPingJia = filterPingJia.filter((item) => {
      //     return item.text != "";
      //   });
      // }
      // return filterPingJia;
    }
  },
  mounted() {
    this.$store.dispatch("getPingJia", () => {
      this.$nextTick(() => {
        new BScroll(".ratings", {
          click: true
        });
      });
    });
  }
};
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import '../../../common/stylus/mixins.styl';

.ratings {
  position: absolute;
  top: 195px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background: #fff;

  .overview {
    display: flex;
    padding: 18px 0;

    .overview-left {
      flex: 0 0 137px;
      padding: 6px 0;
      width: 137px;
      border-right: 1px solid rgba(7, 17, 27, 0.1);
      text-align: center;

      @media only screen and (max-width: 320px) {
        flex: 0 0 120px;
        width: 120px;
      }

      .score {
        margin-bottom: 6px;
        line-height: 28px;
        font-size: 24px;
        color: rgb(255, 153, 0);
      }

      .title {
        margin-bottom: 8px;
        line-height: 12px;
        font-size: 12px;
        color: rgb(7, 17, 27);
      }

      .rank {
        line-height: 10px;
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
    }

    .overview-right {
      flex: 1;
      padding: 6px 0 6px 24px;

      @media only screen and (max-width: 320px) {
        padding-left: 6px;
      }

      .score-wrapper {
        margin-bottom: 8px;
        font-size: 0;

        .title {
          display: inline-block;
          line-height: 18px;
          vertical-align: top;
          font-size: 12px;
          color: rgb(7, 17, 27);
        }

        .star {
          display: inline-block;
          margin: 0 12px;
          vertical-align: top;
        }

        .score {
          display: inline-block;
          line-height: 18px;
          vertical-align: top;
          font-size: 12px;
          color: rgb(255, 153, 0);
        }
      }

      .delivery-wrapper {
        font-size: 0;

        .title {
          line-height: 18px;
          font-size: 12px;
          color: rgb(7, 17, 27);
        }

        .delivery {
          margin-left: 12px;
          font-size: 12px;
          color: rgb(147, 153, 159);
        }
      }
    }
  }

  .split {
    width: 100%;
    height: 16px;
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    background: #f3f5f7;
  }

  .ratingselect {
    .rating-type {
      padding: 18px 0;
      margin: 0 18px;
      border-1px(rgba(7, 17, 27, 0.1));
      font-size: 0;

      .block {
        display: inline-block;
        padding: 8px 12px;
        margin-right: 8px;
        line-height: 16px;
        border-radius: 1px;
        font-size: 12px;
        color: rgb(77, 85, 93);
        background: rgba(77, 85, 93, 0.2);

        &.active {
          background: $green;
          color: #fff;
        }

        .count {
          margin-left: 2px;
          font-size: 8px;
        }
      }
    }

    .switch {
      padding: 12px 18px;
      line-height: 24px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      color: rgb(147, 153, 159);
      font-size: 0;

      &.on {
        .icon-check_circle {
          color: $green;
        }
      }

      .icon-check_circle {
        display: inline-block;
        vertical-align: top;
        margin-right: 4px;
        font-size: 24px;
      }

      .text {
        display: inline-block;
        vertical-align: top;
        font-size: 12px;
      }
    }
  }

  .rating-wrapper {
    padding: 0 18px;

    .rating-item {
      display: flex;
      padding: 18px 0;
      bottom-border-1px(rgba(7, 17, 27, 0.1));

      .avatar {
        flex: 0 0 28px;
        width: 28px;
        margin-right: 12px;

        img {
          border-radius: 50%;
        }
      }

      .content {
        position: relative;
        flex: 1;

        .name {
          margin-bottom: 4px;
          line-height: 12px;
          font-size: 10px;
          color: rgb(7, 17, 27);
        }

        .star-wrapper {
          margin-bottom: 6px;
          font-size: 0;

          .star {
            display: inline-block;
            margin-right: 6px;
            vertical-align: top;
          }

          .delivery {
            display: inline-block;
            height: 12px; // 评论的deliveryTime为空时占位
            vertical-align: top;
            line-height: 12px;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }

        .text {
          margin-bottom: 8px;
          line-height: 18px;
          color: rgb(7, 17, 27);
          font-size: 12px;
        }

        .recommend {
          line-height: 16px;
          font-size: 0;

          .icon-thumb_up, .icon-thumb_down, .item {
            display: inline-block;
            margin: 0 8px 4px 0;
            font-size: 9px;
          }

          .icon-thumb_up {
            color: $yellow;
          }

          .icon-thumb_down {
            color: rgb(147, 153, 159);
          }

          .item {
            padding: 0 6px;
            border: 1px solid rgba(7, 17, 27, 0.1);
            border-radius: 1px;
            color: rgb(147, 153, 159);
            background: #fff;
          }
        }

        .time {
          position: absolute;
          top: 0;
          right: 0;
          line-height: 12px;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
      }
    }
  }
}
</style>



